<template>
  <div>
    <UserNavbar :pageTitle="pageName" />
    <v-container>
      <v-form ref="profileForm" class="pt-8 pb-8" v-model="isProfileFormvalid">
        <h3>Data Diri</h3>
        <v-text-field
          label="Nama"
          type="text"
          v-model="userProfile.name"
          :value="userProfile.name == null ? '' : userProfile.name"
          :rules="this.$commonInputRules.nameRules"
          required
        ></v-text-field>
        <v-text-field
          label="Username"
          type="text"
          v-model="userProfile.username"
          :value="userProfile.username == null ? '' : userProfile.username"
          :rules="this.$commonInputRules.usernameRules"
          required
        ></v-text-field>
        <v-text-field
          label="NISN"
          type="text"
          v-model="userProfile.nisn"
          :value="userProfile.nisn == null ? '' : userProfile.nisn"
          required
        ></v-text-field>
        <v-text-field
          label="Email"
          type="email"
          v-model="userProfile.email"
          :value="userProfile.email == null ? '' : userProfile.email"
          :rules="this.$commonInputRules.emailRules"
          required
        ></v-text-field>
        <v-text-field
          label="Asal Sekolah"
          type="text"
          v-model="userProfile.origin"
          :value="userProfile.origin == null ? '' : userProfile.origin"
          :rules="this.$commonInputRules.originRules"
          required
        ></v-text-field>
        <v-text-field
          label="No Telepon"
          type="text"
          v-model="userProfile.phone"
          :value="userProfile.phone == null ? '' : userProfile.phone"
          :rules="this.$commonInputRules.phoneNumRules"
          required
        ></v-text-field>
        <v-btn
          type="submit"
          @click.prevent="editProfile()"
          class="warning"
          :disabled="!isProfileFormvalid || isEditProfileLoading"
        >Ubah Data Diri</v-btn>
      </v-form>
      <v-form class="pb-8" v-model="isPasswordFormValid">
        <h3>Ubah Password</h3>
        <v-text-field label="Password Lama" type="password" v-model="oldPassword" required></v-text-field>
        <v-text-field
          label="Password Baru"
          type="password"
          v-model="newPassword"
          :rules="this.$commonInputRules.passwordRules"
          required
        ></v-text-field>
        <v-text-field
          label="Konfirmasi Password Baru"
          type="password"
          :rules="getPasswordConfirmationRules"
          required
        ></v-text-field>
        <v-btn
          type="submit"
          @click.prevent="editPassword()"
          class="warning"
          :disabled="!isPasswordFormValid || isEditPasswordLoading"
        >Ubah Password</v-btn>
      </v-form>
    </v-container>
    <v-snackbar v-model="isSnackbarShown" :bottom="true" :timeout="3000">
      {{ snackbarMessage }}
      <template v-slot:action="{ attrs }">
        <v-btn dark text v-bind="attrs" @click="isSnackbarShown = false">Tutup</v-btn>
      </template>
    </v-snackbar>
  </div>
</template>
<script>
import { EventBus } from "@/bus";
import User from "@/models/userModel";
import UserNavbar from "@/components/UserNavbar.vue";
export default {
  name: "Profile",
  components: {
    UserNavbar,
  },

  data: () => ({
    isProfileFormvalid: false,
    isEditProfileLoading: false,
    isPasswordFormValid: false,
    isEditPasswordLoading: false,
    isSnackbarShown: false,
    snackbarMessage: "",
    pageName: "Profil",
    userProfile: new User(),
    oldPassword: "",
    newPassword: "",
  }),
  methods: {
    async fetchUserDetail(userId) {
      try {
        let response = await this.$store.dispatch("user/getDetail", userId);
        if (response.status === 200) {
          this.userProfile = response.data.detail;
        }
      } catch (error) {
        console.log(error);
      }
    },
    async editProfile() {
      this.isEditProfileLoading = true;
      try {
        let response = await this.$store.dispatch(
          "user/editProfile",
          this.userProfile
        );
        if (response.status === 200) {
          this.$store.dispatch("auth/update", response.data.result);
          EventBus.$emit("onProfileUpdated");

          this.snackbarMessage = "Profil berhasil diperbarui.";
          this.isSnackbarShown = true;
        }
      } catch (error) {
        console.log(error);
      }
      this.isEditProfileLoading = false;
    },
    async editPassword() {
      this.isEditPasswordLoading = true;
      let data = {
        id: this.userProfile.id,
        oldPassword: this.oldPassword,
        newPassword: this.newPassword,
      };
      try {
        let response = await this.$store.dispatch("user/editPassword", data);
        if (response.status === 200) {
          this.snackbarMessage = "Password berhasil diperbarui.";
          this.isSnackbarShown = true;
        }
      } catch (error) {
        console.log(error);
      }
      this.isEditPasswordLoading = false;
    },
  },
  computed: {
    getUserDetail() {
      return this.$store.getters["user/userProfileData"];
    },
    getUserId() {
      return this.$store.getters["auth/userId"];
    },
    getPasswordConfirmationRules() {
      return [
        (v) =>
          (!!v && v) === this.newPassword || "Masukkan password yang sama.",
      ];
    },
  },
  created() {
    if (this.getUserDetail) {
      this.userProfile = this.getUserDetail;
    } else {
      this.fetchUserDetail(this.getUserId);
    }
  },
};
</script>